.order-app {
    background: url(../../img/hero.jpg) top;
    background-attachment: fixed;
    background-size: 100% 100%;
    // width: 100%;
    min-height: 100vh;
    // height: 100vh;
    margin: 0;
    padding: 32px;

    .title {
        color: white;
    }

    .add-btn {
        margin: 0;
        font-size: 16px;
        padding: 10px 16px;
        // padding: 10px;
    }

    .refresh-btn {
        margin: 0;
        justify-self: flex-end;
    }

    table {
        width: 100%;
        margin: 30px auto 80px auto;
        border-spacing: 0;
        border-collapse: collapse;

        td,
        th {
            padding: 6px 6px;
            // padding: 12px 5px;
            border: 1px solid #ddd;
            text-align: center;
        }


        thead:first-child {
            background-color: cornflowerblue;
            color: white;
        }

        tbody tr:nth-child(even) {
            background-color: #ffffff;
        }

        tbody tr:nth-child(odd) {
            background-color: var(--color-info-dark);
        }

    }

    .edit-order {
        background: var(--color-white);
        border-radius: var(--card-border-radius);
        margin-top: 1rem;
        box-shadow: var(--box-shadow);
        transition: all 300ms ease;
        gap: 10px;
        width: 50%;
        align-items: center;
        padding: 30px;
        .close-btn{
            align-self: flex-start;
            margin: 0;
        }
        div{
            border: 1px black solid;
            border-radius: 10px;
            padding: 8px;
            
        }
        &:hover {
           box-shadow: none;
           transition: all 300ms ease;
        }
        input{
            
            border: gray 1px solid;
            border-radius: 10px;
            padding: 8px;
        }

        .order-edit-form {
            flex-wrap: wrap;
            gap: 10px;
            margin: 0;
            div{
                margin: 0;
            }
            
            
            select {
                border: gray 1px solid;
                border-radius: 10px;
                padding: 8px;
                min-height: 24px;
                height: 100%;
            }
        }
    }



}


@media(max-width:970px) {

    table {
        display: block;
        width: 100%;

        thead {
            display: none;
        }

        tbody,
        tr,
        td {
            display: block;
            width: 100%;

            &.package-id {
                display: none;
            }

        }
    }



}